/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { DropdownMenu } from '@safe-wordpress/components';
import { useDispatch, useSelect } from '@safe-wordpress/data';

/**
 * External dependencies
 */
import {
	STORE_NAME as NAB_SEGMENTS,
	createSegmentationRule as createSegmentationRuleWithDefaults,
} from '@nab/segmentation-rules';
import type {
	SegmentationRuleCategory,
	SegmentationRuleCategoryName,
	SegmentationRuleTypeName,
} from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import { STORE_NAME as NAB_EDITOR } from '../../../store/config';

export type SegmentationRuleTypeCategoryProps = {
	readonly category: SegmentationRuleCategory;
};

export const SegmentationRuleTypeCategory = ( {
	category,
}: SegmentationRuleTypeCategoryProps ): JSX.Element | null => {
	const segmentationRuleTypes = useSegmentationRuleTypes( category.name );
	const activeSegmentRules = useActiveSegmentRules();
	const createSegmentationRule = useSegmentationRuleCreator();
	const Icon = category.icon;

	if ( ! segmentationRuleTypes.length ) {
		return null;
	} //end if

	return (
		<li
			key={ category.name }
			className="nab-segmentation-rule-type-category"
		>
			<DropdownMenu
				icon={ <Icon /> }
				className="nab-segmentation-rule-type-category__item"
				label={ category.title }
				controls={ segmentationRuleTypes.map(
					( { name, title, icon: TypeIcon } ) => ( {
						title,
						icon: <TypeIcon />,
						isDisabled: activeSegmentRules.some(
							( rule ) => rule.type === name
						),
						onClick: () => createSegmentationRule( name ),
					} )
				) }
				toggleProps={ {
					className: 'nab-segmentation-rule-type-category__toggle',
					disabled: segmentationRuleTypes.every( ( ruleType ) =>
						activeSegmentRules.some(
							( rule ) => rule.type === ruleType.name
						)
					),
				} }
				menuProps={ {
					className: 'nab-segmentation-rule-type-category__menu',
				} }
			/>
		</li>
	);
};

// =====
// HOOKS
// =====

const useSegmentationRuleTypes = ( catName: SegmentationRuleCategoryName ) =>
	useSelect( ( select ) => {
		const ruleTypes =
			select( NAB_SEGMENTS ).getSegmentationRuleTypes() || [];
		return ruleTypes.filter( ( { category } ) => category === catName );
	} );

const useActiveSegmentRules = () => {
	const activeSegment = useActiveSegment();
	return useSelect( ( select ) => {
		const { getSegmentationRules } = select( NAB_EDITOR );
		return activeSegment?.id
			? getSegmentationRules( activeSegment.id ) || []
			: [];
	} );
};

const useActiveSegment = () =>
	useSelect( ( select ) => select( NAB_EDITOR ).getActiveSegment() );

const useSegmentationRuleCreator = () => {
	const { addSegmentationRulesIntoSegment } = useDispatch( NAB_EDITOR );
	const activeSegment = useActiveSegment();
	return ( type: SegmentationRuleTypeName ) => {
		if ( ! activeSegment ) {
			return;
		} //end if
		const rule = createSegmentationRuleWithDefaults( type );
		if ( rule ) {
			addSegmentationRulesIntoSegment( activeSegment.id, rule );
		} //end if
	};
};
