/**
 * External dependencies
 */
import { keyBy, omit } from 'lodash';
import type { AnyAction } from '@nab/types';

/**
 * Internal dependencies
 */
import { INIT_STATE as IS } from '../config';

import type { State as FullState } from '../types';
type State = FullState[ 'experiment' ][ 'segments' ];

import type { SegmentAction } from '../actions/segments';
import type { SetupEditor } from '../actions/editor';
type Action = SegmentAction | SetupEditor;

const INIT_STATE = IS.experiment.segments;

export function segments( state = INIT_STATE, action: AnyAction ): State {
	return actualReducer( state, action as Action ) ?? state;
} //end segments()

function actualReducer( state: State, action: Action ): State {
	switch ( action.type ) {
		case 'ADD_SEGMENTS':
			return {
				...state,
				...keyBy( action.segments, 'id' ),
			};

		case 'UPDATE_SEGMENT': {
			const oldSegment = state[ action.segmentId ];
			if ( ! oldSegment ) {
				return state;
			} //end if

			return {
				...state,
				[ action.segmentId ]: {
					...oldSegment,
					attributes: {
						...oldSegment.attributes,
						...action.attributes,
					},
				},
			};
		}

		case 'REMOVE_SEGMENTS':
			return omit( state, action.ids );

		case 'SETUP_EDITOR': {
			if ( 'nab/heatmap' === action.experiment.type ) {
				return {};
			} //end if

			const initialSegments = action.experiment.segments.map(
				( segment ) => omit( segment, 'segmentationRules' )
			);
			return keyBy( initialSegments, 'id' );
		}
	} //end switch
} //end actualReducer()
