/**
 * WordPress dependencies
 */
import { registerStore, controls } from '@safe-wordpress/data';

/**
 * External dependencies
 */
import type {
	Alternative,
	Dict,
	Maybe,
	OmitFirstArgs,
	PromisifyReturnTypes,
	RemoveReturnTypes,
} from '@nab/types';

/**
 * Internal dependencies
 */
import reducer from './reducer';
import * as selectors from './selectors';
import * as realActions from './actions';
import * as sideEffects from './side-effects/actions';
import { STORE_NAME } from './config';

export { STORE_NAME } from './config';

const actions = { ...realActions, ...sideEffects };

registerStore( STORE_NAME, {
	reducer,
	controls,
	// eslint-disable-next-line
	actions: actions as any,
	selectors,
} );

// ==========
// TYPESCRIPT
// ==========

import type { ExperimentAttributes, HeatmapAttributes } from './types';

type Actions = RemoveReturnTypes< typeof realActions > &
	PromisifyReturnTypes< RemoveReturnTypes< typeof sideEffects > >;

type Selectors = OmitFirstArgs<
	Omit<
		typeof selectors,
		'getAlternative' | 'getExperimentAttribute' | 'getHeatmapAttribute'
	>
> & {
	readonly getAlternative: < A extends Dict = Dict >(
		id: string
	) => Maybe< Alternative< A > >;
	readonly getExperimentAttribute: < K extends keyof ExperimentAttributes >(
		attribute: K
	) => ExperimentAttributes[ K ];
	readonly getHeatmapAttribute: < K extends keyof HeatmapAttributes >(
		attribute: K
	) => Maybe< HeatmapAttributes[ K ] >;
};

declare module '@wordpress/data' {
	function dispatch( key: typeof STORE_NAME ): Actions;
	function select( key: typeof STORE_NAME ): Selectors;
}
