/**
 * External dependencies
 */
import { values } from 'lodash';
import createSelector from 'rememo';
import type { SegmentId, SegmentationRule } from '@nab/types';

/**
 * Internal dependencies.
 */
import type { State } from '../types';

export const getSegmentationRules = createSelector(
	( state: State, segmentId: SegmentId ): ReadonlyArray< SegmentationRule > =>
		values( state.experiment.segmentationRules[ segmentId ] ),
	( state: State, segmentId: SegmentId ) => [
		state.experiment.segmentationRules,
		segmentId,
	]
);
