/**
 * External dependencies
 */
import { keyBy, omit } from 'lodash';
import type { AnyAction } from '@nab/types';

/**
 * Internal dependencies
 */
import { INIT_STATE as IS } from '../config';

import type { State as FullState } from '../types';
type State = FullState[ 'experiment' ][ 'segmentationRules' ];

import type { SRAction } from '../actions/segmentation-rules';
import type { SetupEditor } from '../actions/editor';
type Action = SRAction | SetupEditor;

const INIT_STATE = IS.experiment.segmentationRules;

export function segmentationRules(
	state = INIT_STATE,
	action: AnyAction
): State {
	return actualReducer( state, action as Action ) ?? state;
} //end segmentationRules()

function actualReducer( state: State, action: Action ): State {
	switch ( action.type ) {
		case 'ADD_SEGMENTATION_RULES_INTO_SEGMENT':
			return {
				...state,
				[ action.segmentId ]: {
					...state[ action.segmentId ],
					...keyBy( action.segmentationRules, 'id' ),
				},
			};

		case 'REPLACE_SEGMENTATION_RULES_IN_SEGMENT':
			return {
				...state,
				[ action.segmentId ]: {
					...keyBy( action.segmentationRules, 'id' ),
				},
			};

		case 'UPDATE_SEGMENTATION_RULE': {
			const segment = state[ action.segmentId ];
			if ( ! segment ) {
				return state;
			} //end if

			const oldSegmentationRule = segment[ action.segmentationRuleId ];
			if ( ! oldSegmentationRule ) {
				return state;
			} //end if

			return {
				...state,
				[ action.segmentId ]: {
					...segment,
					[ action.segmentationRuleId ]: {
						...oldSegmentationRule,
						attributes: {
							...oldSegmentationRule.attributes,
							...action.attributes,
						},
					},
				},
			};
		}

		case 'REMOVE_SEGMENTATION_RULES_FROM_SEGMENT':
			return {
				...state,
				[ action.segmentId ]: omit(
					state[ action.segmentId ],
					action.segmentationRuleIds
				),
			};

		case 'SETUP_EDITOR':
			if ( 'nab/heatmap' === action.experiment.type ) {
				return {};
			} //end if

			return action.experiment.segments.reduce( ( memo, segment ) => {
				memo[ segment.id ] = keyBy( segment.segmentationRules, 'id' );
				return memo;
			}, {} as State );
	} //end switch
} //end actualReducer()
